body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.home-header {
    background: linear-gradient(100deg,#FF8C00, #FFA500);
    width: 100vw;
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.home-container {
    background: black
}
.header-btn:hover {
    border-color: white;
}

.home-heading {
    padding: 0px;
    margin: 0px;
    font-size: 44px;
    margin-left: 30px;
    color: white;
    font-family: 'Kaushan Script', cursive;
}

.home-main {
    margin: auto;
    margin-top: -50px;
    width: 100vw;
    height: 100vh;    
    background: linear-gradient(rgba(0,0,0,0.2), rgba(0, 0, 0, 0.8)), url(/sign_language_recognition/static/media/intro.752cb5e0.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size:  750px;
}

.description {
    font-size: 66px;
    color: white;
    text-align: center;
    margin: 0px;
    margin-top: 10px;
    font-family: 'Raleway', sans-serif;
    letter-spacing: 0.5px;
}

.btn-section {
    position: absolute;
    left: 38vw;
    top: 60vh;
}

.btn{
    padding: 10px;
    padding-right: 15px;
    padding-left: 15px;
    margin: 10px 20px 10px 20px;
    background-color: rgba(255, 255, 255, 0);
    color: white;
    font-size: 24px;
    border-color: #FFA500;
    border-radius: 8px;
    border-width: 4px;
    cursor: pointer;
}
.btn:hover {
    background: rgba(255,165,0, 0.2);
    color: white;
}

body::-webkit-scrollbar {
    display: none;
}
#about-btn {
    padding: 5px 10px;
}
#about-btn:hover {
    border-color: white;
    background-color: white;
    color: black;
}
#about-btn:focus {
    box-shadow: none;
    background: white;
    border-color: white;
    color: black;
}
.sign-container {
  background: linear-gradient(100deg, #FF8C00, #FFA500);
  min-width: 100vw;
  min-height: 100vh;
}

.secondary-btn {
  position: relative;
  left: 45%;
  padding: 10px 20px 10px 20px;
  border-radius: 5px;
  border-color: rgba(125, 125, 125, 0.1);
  border-width: 5px;
  background-color: rgba(0, 0, 0,0.2);
  box-shadow: 0px 0px 0px 0px;
  color: white;
}

.secondary-btn:hover {
  background-color: rgba(0, 0, 0,0.4);
}

.starting-contdown {
  margin: auto;
  border: 10px solid rgba(125, 125, 125, 0.5);
  width: -webkit-max-content;
  width: max-content;
  padding: 10px 30px;
  border-radius: 50%;
  background-color: rgba(255,255,255,0);
  position: relative;
  top: 45%;
}
.starting-contdown h1 {
  color: white;
}
.pose-img {
  position: absolute;
  right: 120px;
  top: 80px;
  width: 500px;
  aspect-ratio: 1;
}

.performance-container {
  display: flex;
  padding: 20px;
  position: absolute;
  left: 150px;
}


.pose-performance {
  margin: 10px 10px;
  background-color: white;
  border-radius: 8px;
  padding: 10px;
}
.pose-performance h4 {
  color: rgba(0,0,0,0.7);
}

#webcam {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

#my-canvas {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}
#pose-dropdown-btn:hover {
    border-color: white;
}
/* #pose-dropdown-btn:active {
    background-color: white;
} */
#pose-dropdown-btn {
    width: 400px;
}



.dropdown-container {
    display: flex;
    justify-content: center;
}
#pose-dropdown-btn:focus {
    background-color: white;
    color: black;
    box-shadow: none;
}
#pose-dropdown-btn:focus-within {
    border-color: white;
}
.dropdown-option {
    background: url(/sign_language_recognition/static/media/R.367c9d14.png);
}

.dropdown-custom-menu {
    overflow: scroll;
    height: 40vh;
    scrollbar-width: none;
    -ms-overflow-style: none;
}

.dropdown-custom-menu::-webkit-scrollbar {
    display: none;
}

.dropdown-item-container {
    width: 400px;
    display: flex;
    border-radius: 4px;
    justify-content: center;
    align-items: center;
}

.dropdown-item-1 {
    margin: 10px;
}
.dropdown-img {
    width: 80px;
    height: 80px;
}
body, html {
  margin: 0;
  padding: 0;
}

